import React from 'react';

import './Content.css';

export class Behandling extends React.Component{

  render(){
    var content = <div>
      <h2>Behandling</h2>
      <h3>Sjukgymnastik - Frsta Besket</h3>
      <p>
        Vid det frsta besket grs en grundlig underskning och kartlggning av din nuvarande status. Utifrn det stlls en funktionsdiagnos. Du fr med dig ett individuellt anpassat rehabiliteringsprogram som fljs upp kontinuerligt. Rehabiliteringen kan best av trning, manuella tekniker, akupuktur eller tejpning.
      </p>

      <h3>Rrelseanalys</h3>
      <p>
        Genom olika tester gr vi igenom ditt rrelsemnster med syftet att upptcka och medvetandegra ogynsamma rrelser. Utifrn det bygger vi upp ett trningsprogram som hjlper dig att plocka fram potentialen i ditt rrelsemnster!
Rrelseanalys r ett bra redskap fr dig som vill frbttra din teknik, rra dig lttare och arbeta skadefrebyggande.
      </p>

      <h3>Idrottsmassage</h3>
      <p>
        Djupgende massage med syfte att ka blodcirkulation och pskynda terhmtning.
      </p>

      <p>
        Kliniken ligger i Sportgym res lokaler och hr har vi tillgng till fullt utrustat gym.
      </p>
    </div>

    return(
      <div className="container">
        <div className="cont-dev">

          <img src={require('./Behandling/beh-bild1.JPG')} alt={'Behandling-bild'} width="100%" />
            {content}
        </div>
        <div className="cont-web">
              {content}
        </div>
      </div>
      )
  }
}



